.slider {
  width: calc(60% - 1px);
  min-width: 320px;
  max-width: 1000px; // ширина самого узкого изображения
  position: relative;
  margin-bottom: 2.5em;

  .slider__tip > p {
    display: none;
  }

  .slider__list {
    display: flex;
    width: 100%;
    outline: none;
    list-style: none;
    box-shadow: 0 0 10px $dcg-3;
    transition: box-shadow 0.2s ease;

    overflow-x: auto;
    scroll-snap-type: x mandatory;

    &:focus,
    &:hover {
      box-shadow: 0 0 10px $dcg-1;
    }

    &:focus ~ .slider__tips .slider__tip--focus > p,
    &:hover ~ .slider__tips .slider__tip--hover > p {
      display: block;
    }

    &:hover ~ .slider__tips .slider__tip--focus > p {
      display: none;
    }

    &:focus:hover ~ .slider__tips .slider__tip--focus > p,
    &:focus:hover ~ .slider__tips .slider__tip--hover > p {
      display: none;
    }

    &:focus:hover ~ .slider__tips .slider__tip--focus-and-hover > p {
      display: block;
    }
  }

  .slider__item {
    flex-basis: 100%;
    flex-shrink: 0;
    margin-right: 1px;

    scroll-snap-align: start;
    scroll-snap-stop: always;
  }

  figure {
    @include zero-edges;
    @include column-flex;
    width: 100%;
  }

  .padded-box {
    @include padded-box;
    padding-bottom: $ratio-large;
    margin-bottom: 1em;

    @media (max-width: $pre-desktop-width) {
      margin-bottom: 0.5em;
    }
  }

  img {
    @include content-in-padded-box;
  }

  figcaption {
    font-style: italic;
    padding-bottom: 0.5em;

    @media (max-width: $pre-desktop-width) {
      margin-bottom: 0.75em;
    }
  }

  .slider__controls {
    position: absolute;
  }

  .slider__dots {
    top: 0;
    left: 0;

    display: flex;
    align-items: flex-start;
    padding-top: 0.5em;
    padding-left: 0.5em;
    line-height: 0;
  }

  .slider__dot {
    &:not(:last-child) {
      margin-right: 0.4em;
    }
  }

  .slider__dot-btn {
    width: 1em;
    height: 1em;
    border: 1px solid $lcg-1;
    border-radius: 50%;
    outline: none;
    background-color: transparent;
    cursor: pointer;

    @include zero-edges;

    &.active {
      background-color: $lcg-1;
    }

    &:focus {
      box-shadow: 0 0 5px $lcg-1;
    }

    &:hover {
      background-color: $lcg-3;
    }
  }

  .slider__arrows {
    top: 100%;
    left: 0;

    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0.2em 0.5em 1.1em;
    transform: translate(0, -100%);

    background-color: transparent;
    background-image:
      linear-gradient(to bottom, transparent 70%, $lcg-1 70%, $lcg-1),
      linear-gradient(to right, $lcg-1, transparent 40%, transparent, transparent 60%, $lcg-1);

    @media (max-width: $pre-desktop-width) {
      padding-top: 0.1em;
      padding-bottom: 0.75em;
    }
  }

  .slider__arrow-btn {
    @include zero-edges;
    @include centering-flex;

    outline: none;
    border: none;
    background-color: $lcg-1;

    width: 3.3em;
    height: 3.3em;
    border-radius: 50%;
    box-shadow: 0 0 0.5em $dcg-3;

    transition: all 0.3s ease-in;
    cursor: pointer;

    // @media (max-width: $pre-desktop-width) {
    //   width: 35px;
    //   height: 35px;
    //   transform: translateY(-12px);
    // }

    &:before {
      content: "";
      display: block;
      width: 30%;
      height: 30%;
      border: 0.2em solid $dcg-3;
      border-right-color: transparent;
      border-bottom-color: transparent;
    }

    &--previous {
      &:before {
        transform: translateX(0.2em) rotate(-45deg);
      }
    }

    &--next {
      &:before {
        transform: translateX(-0.25em) rotate(135deg);
      }
    }

    &:focus,
    &:hover {
      box-shadow: inset 0 0 0.5em $dcg-1;
    }

    &:active {
      background-color: $dcg-3;

      &:before {
        border-top-color: $lcg-1;
        border-left-color: $lcg-1;
      }
    }
  }
}

